import { graphql, withPrefix } from 'gatsby'
import React, { useCallback, useEffect, useState, useRef, useMemo } from 'react'
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

// import { responsive } from '../components/ThemeProvider/theme'
import Layout from '../containers/Layout'
import Question from './question'

let loaded

const Q = ({ data, pageContext }) => {
  const { id } = pageContext
  const { length } = data.allQuestionsJson.nodes
  const [curentId, setCurrentId] = useState(id - 1)
  const slickRef = useRef()
  const handleNext = useCallback((i) => {
    slickRef.current.slickNext()
    setCurrentId(i + 1)
  }, [])
  const handlePrev = useCallback((i) => {
    slickRef.current.slickPrev()
    setCurrentId(i - 1)
  }, [])
  useEffect(() => {
    if (loaded) {
      window.history.pushState(null, null, withPrefix(`/q/${curentId + 1}`))
    }
    loaded = true
  }, [curentId])
  return (
    <Layout
      noHeader
      bg="white"
      title={`第${curentId + 1}題`}
      footerProps={{
        position: 'absolute',
        transform: 'translateX(-50%)',
        left: '50%',
        bottom: '0.75em',
      }}
    >
      {useMemo(() => (
        <Slider
          autoplay={false}
          arrows={false}
          dots={false}
          infinite={false}
          draggable={false}
          ref={slickRef}
          speed={1000}
          initialSlide={curentId}
        >
          {data.allQuestionsJson.nodes.map((d, i) => (
            <div key={d.id}>
              <Question
                data={d}
                onNext={() => handleNext(i)}
                onPrev={() => handlePrev(i)}
                pageContext={{ id: d.id, length, isLast: i === length - 1 }}
              />
            </div>
          ))}
        </Slider>
      ), [curentId, data.allQuestionsJson.nodes, handleNext, handlePrev, length])}
    </Layout>
  )
}

export default Q

export const query = graphql`
  query QuestionPageQuery {
    allQuestionsJson {
      nodes {
        id
        question
        options
      }
    }
  }
`
